import { KeyboardEvent } from "react"
import { z } from "zod"
import { CqsScopeSchema } from "@rp/common/api-types"
import { Box, Chip } from "@rp/react/web/mui/core"
import { Add } from "@rp/react/web/mui/icons"

type Column = {
  label: string
  title: string
  prompt: string
  timeRange?: string
  sourceId?: z.infer<typeof CqsScopeSchema>
}

const COLUMNS: Array<Column> = [
  {
    label: "Macro Impact",
    title: "Macro Impact",
    prompt: "How does management describe the impact of interest rates or inflation?",
    timeRange: "last 90 days",
    sourceId: "quartr_pro",
  },
  {
    label: "Revenue Guidance",
    title: "Revenue Guidance",
    prompt: "What are the numerical ranges for forward-looking revenue guidance?",
  },
  {
    label: "Tariff Exposure",
    title: "Tariff Exposure",
    prompt: "Explain exposure to U.S. import tariffs, focusing on supply chain vulnerabilities.",
  },
]

type Props = {
  onColumnClick: (column: Column) => void
  onKeyDown: (event: KeyboardEvent<HTMLDivElement>) => void
}

function SuggestedColumns({ onColumnClick, onKeyDown }: Props) {
  return (
    <Box sx={{ display: "flex", gap: 1 }}>
      {COLUMNS.map(column => (
        <Chip
          onKeyDown={event => {
            onColumnClick(column)
            onKeyDown(event)
          }}
          key={column.label}
          sx={theme => ({
            fontSize: 16,
            "& svg": { fill: theme.palette.text.primary, fontSize: 18 },
          })}
          size="medium"
          icon={<Add />}
          label={column.title}
          onClick={ev => {
            ev.stopPropagation()
            onColumnClick(column)
          }}
        />
      ))}
    </Box>
  )
}

export { SuggestedColumns }
